import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  location: null,
  loading: false,
  error: null,
  isLocationSet: false,
};

const locationSlice = createSlice({
  name: "location",
  initialState,
  reducers: {
    setLocationStart: (state) => {
      state.loading = true;
      state.error = null;
    },
    setAutoLocationSuccess: (state, action) => {
      state.location = action.payload;
      state.loading = false;
      state.error = null;
      state.isLocationSet = true;
    },
    setLocationFailure: (state, action) => {
      state.loading = false;
      state.error = action.payload;
      state.isLocationSet = false;
    },
    setManualLocation: (state, action) => {
      state.location = action.payload;
      state.loading = false;
      state.error = null;
      state.isLocationSet = true;
    },
    clearLocation: (state) => {
      state.location = null;
      state.loading = false;
      state.error = null;
      state.isLocationSet = false;
    },
  },
});

export const {
  setLocationStart,
  setAutoLocationSuccess,
  setLocationFailure,
  setManualLocation,
  clearLocation,
} = locationSlice.actions;

export default locationSlice.reducer; 